@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  padding: 5%;
  background-color: #d5bfbf;
}
.main{
  background-color: rgb(165, 223, 236);
}


.card{
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 10px 10px 10px 10px;
    margin: 15px;
   
}


.form{
    display: flex;
    flex-direction: column;

}
.b{
    display: flex;
    justify-content: center;
}

.button{
    display: flex;
    width: 50%;
    height: 30px;
    background-color: aqua;
    box-shadow: 1px 1px;
    border-radius: 10px;
    text-align: center;
}
.button:hover{
    background-color: rgb(45, 244, 244);
}
.ExpensesFilter{
    display: flex;
    justify-content: space-between;
}
.yearDropdown{
    font: inherit;
    padding: 0.5rem 3rem;

    border-radius: 6px;
}
.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    padding: 0.5rem;
    margin: 1rem 0;
    
    background-color: #399522;
  }
  .expense-item:hover{
  background-color: #718f4c;
  }
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1 1;
  }
  
  .expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
    color: white;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
  }
  
  @media (min-width: 580px) {
    .expense-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1 1;
    }
  
    .expense-item__description h2 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
  .date{
    border: 2px solid black ;

    border-radius: 5px;
    display: flex;
    /* flex-direction: column; */
    justify-content: space-around;
    justify-items:center;
  }
  .dateInside{
    padding: 5px;
  }
.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .chart-bar__inner {
    height: 100%;
    width: 100%;
    border: 1px solid #313131;
    border-radius: 12px;
    background-color: #c3b4f3;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .chart-bar__fill {
    background-color: #4826b9;
    width: 100%;
    transition: all 0.3s ease-out;
  }
  
  .chart-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
  }
.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: #f8dfff;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
  }
